export const NoDataDisplay = ({
  noImage = false,
  dataName,
  isFromAddPage,
  messageCustom = null,
  pageAttr
}) => {
  const { t } = useTranslation()

  let messageText = !isFromAddPage
    ? t(
        messageCustom ??
          (pageAttr?.crudOpt?.disabledAdd
            ? "There are no records to display. Try changing the filter"
            : "There are no records to display. Try changing the filter or adding new data")
      )
    : t("Add one or more data here...")

  return (
    <div
      className="align-items-center w-100"
      style={{
        padding: "24px",
        textAlign: "center",
        minHeight: !noImage ? "370px" : "auto"
      }}
    >
      {!noImage && (
        // <img
        //   style={{ maxWidth: "50%", minWidth: "35%" }}
        //   className="mb-1 no-data-grid"
        //   src={noDataDisplayImg}
        // />
        // <img
        //   style={{ height: "250px" }}
        //   className="mb-1 no-data-grid"
        //   src={noDataAnim}
        // />

        <div
          style={{
            // maxHeight: "210px",
            //  maxWidth: "16%",
            width: "35%",
            margin: "auto"
          }}
        >
          <NoDataAnim />
        </div>
      )}
      <h4 className="text-secondary">{t("No Data")}</h4>
      <div className="text-secondary">
        {!dataName
          ? messageText
          : messageText
              .replaceAll("data", `data ${t(dataName)}`)
              .replaceAll("records", `${t(dataName)} records`)}
      </div>
      {/* <Alert color="info">
        <h4 className="alert-heading">{t("No Records Find")}</h4>
        <div className="alert-body">
          {t(
            "There are no records to display. Try changing the filter or adding new data"
          )}
        </div>
      </Alert> */}
    </div>
  )
}

